// ----- ui -----
// TopSendName.jsx
.from_name {
  background: $formBackColor;
  border: solid 5px $mainColor;
  border-radius: 10px;
  width: 90%;
  margin: 30px auto 0;
  padding: 15px 0;
  text-align: center; }

.label_name {
  font-size: 14px;
  font-weight: bold; }

input {
  & > .input_name {}
  height: 3em;
  display: block;
  margin: 10px auto; }

button.send_name {
  width: 150px;
  font-size: $fontSize;
  color: $formButtonFontColor;
  font-weight: bold;
  text-decoration: none;
  text-align: center;
  display: block;
  padding: 8px 0 10px;
  margin: 0 auto;
  background-color: $BtnBackColor;
  border-radius: 5px;
  &[disabled] {
    background-color: $BtnDisabledBackColor; } }


